@font-face {
    font-family: Poppins-semibold;
    src: url(./Poppins/Poppins-SemiBold.ttf);
}

@font-face {
    font-family: Poppins-bold;
    src: url(./Poppins/Poppins-Bold.ttf);
}

@font-face {
    font-family: Poppins-regular;
    src: url(./Poppins/Poppins-Regular.ttf);
}

@font-face {
    font-family: Poppins-medium;
    src: url(./Poppins/Poppins-Medium.ttf);
}

.bg-cover{
    background-image:linear-gradient(180deg, #9D0E0E00 0%, #4F0707 100%),url(./bg-img.png);
    height: 480px;
    width: 100%;
    background-size:cover;
    background-repeat: no-repeat;
    background-position: center;
}
.navbar{
    margin-left: 66rem;
    margin-top: 17px;
    font-family: poppins-Medium;
    font-size: 16px;
}
.navbar button{
    width: 181px;
    height: 46px;
}

.logo{
    background: #FFFFFF;
    width: 147px;
    height: 147px;
    border-radius: 50%;
    margin-left: 560px; 
}

.logo a{
    font-family: poppins-semibold ;
    font-size:70px ;
    color: #EB2929;
    text-decoration-line: none;
    text-decoration-color: initial;
    display: block;
    padding: 12% 27%;
}

.head-text{
    color: #FFFFFF;
    font-family: poppins-semibold;
    font-size: 36px;
    text-align: center;
    margin-top: 22px;
}

.input-group input{
    height: 60px;
}
.dropdown button{
    width: 245px;
    height: 60px;
    margin-left: 270px;
}
.input-group{
    margin: 0 -120px;
    font-family: poppins-regular;
    font-size: 15px;
    color: #636F88;
}
.search-box{
    width: 477px;
    height: 60px;
    border-color: #FFFFFF;
}
#quick-search{
    margin-top: 38px;
    font-family: poppins-regular;
    font-size: 15px;
    color: #636F88;
}
#qfont {
    margin-top: 48px;
    margin-left: 18px;
}
#qfont h3{
    font-family: poppins-bold;
    font-size: 30px;
    color: #192F60;
} 

#qfont p{
    font-family: poppins-regular;
    font-size: 18px;
    color: #8C96AB;
} 
.box{
   border: 1px;
   background: #FFFFFF;
   box-shadow: 0px 3px 6px #00000029;
   width: 360px;
   height: 160px;
   margin-left: 30px;
   margin-bottom: 26px;
   cursor: pointer;
}
.l-box img{
    width: 360px;
   height: 160px;
   object-fit: cover;
}
.r-box h3{
    font-family: poppins-semibold;
    font-size: 18px;
    padding-left: 29px;
    padding-top: 25px;
    padding-right: 83px;
}
.r-box p{
    font-family: poppins-regular;
    font-size: 14px;
    padding-left: 29px;
    padding-right: 33px;
    padding-bottom: 44px;
}

a {
    color: initial;
    text-decoration: none;
}